import {
  GET_CELL_FILE_SUCCESS,
  GET_CELL_NAME_FILE_SUCCESS,
  GET_CELL_IMAGE_FILE_SUCCESS,
} from "../actionTypes/cell";
import {
  SHOW_RASP_POPUP,
  SHOW_ACTION_POPUP,
  CLOSE_POPUP,
  SHOW_INFO_POPUP,
  SHOW_NO_TYPE_POPUP,
  BEGIN_CUSTOM_SELECT,
  SUBMIT_NO_TYPE_POPUP,
  CELL_TO_UPDATE_CUSTOM_CELL_SELECTED,
  CUSTOM_CELLS_SUBMITED,
  CANCEL_CUSTOM_CELLS,
  SHOW_EMPTY_CELL_POPUP,
  SHOW_IMAGE_CELL_POPUP,
  SHOW_FORMAT_CELLS_POPUP,
  SHOW_ADD_TO_USER_POPUP,
} from "../actionTypes/popups";

const initialState = {
  raspberryPopupOpen: false,
  actionPopupOpen: false,
  infoPopupOpen: false,
  noTypePopupOpen: false,
  emptyCellPopupOpen: false,
  imagePopupOpen: false,
  formatCellsPopupOpen: false,
  addToUserPopupOpen: false,
  cell: undefined,
  cellsRow: undefined,
  cellFile: undefined,
  cellNameFile: undefined,
  cellImageFile: undefined,
  cellToUpdate: undefined,
  cellToUpdateCustomCells: undefined,
  letsUpdate: false,
};

export default function snackbarReducer(state = initialState, action = {}) {
  switch (action.type) {
    case SHOW_RASP_POPUP: {
      return {
        ...state,
        raspberryPopupOpen: true,
      };
    }
    case CANCEL_CUSTOM_CELLS: {
      return {
        ...state,
        cellToUpdate: undefined,
        cellToUpdateCustomCells: undefined,
        letsUpdate: !state.letsUpdate,
      };
    }
    case CUSTOM_CELLS_SUBMITED: {
      return {
        ...state,
        cellToUpdate: undefined,
        cellToUpdateCustomCells: undefined,
        letsUpdate: !state.letsUpdate,
      };
    }
    case SHOW_ACTION_POPUP: {
      return {
        ...state,
        actionPopupOpen: true,
        cell: action.cell,
        cellsRow: action.cellsRow,
      };
    }
    case BEGIN_CUSTOM_SELECT: {
      return {
        ...state,
        actionPopupOpen: false,
        cellToUpdate: action.cell,
        cellToUpdateCustomCells: [],
      };
    }
    case CELL_TO_UPDATE_CUSTOM_CELL_SELECTED: {
      const cells = state.cellToUpdateCustomCells;
      if (cells.includes(action.cell)) {
        const index = cells.indexOf(action.cell);
        cells.splice(index, 1);
      } else {
        cells.push(action.cell);
      }
      return {
        ...state,
        cellToUpdateCustomCells: cells,
        letsUpdate: !state.letsUpdate,
      };
    }
    case SHOW_ADD_TO_USER_POPUP: {
      return {
        ...state,
        addToUserPopupOpen: true,
      };
    }
    case SUBMIT_NO_TYPE_POPUP: {
      return {
        ...state,
        noTypePopupOpen: false,
        actionPopupOpen: action.isAction,
        infoPopupOpen: !action.isAction,
      };
    }
    case SHOW_NO_TYPE_POPUP: {
      return {
        ...state,
        noTypePopupOpen: true,
        cell: action.cell,
        cellsRow: action.cellsRow,
      };
    }
    case SHOW_EMPTY_CELL_POPUP: {
      return {
        ...state,
        emptyCellPopupOpen: true,
        cell: action.cell,
        cellsRow: action.cellsRow,
      };
    }
    case SHOW_IMAGE_CELL_POPUP: {
      if (action.cell !== undefined) {
        return {
          ...state,
          imagePopupOpen: true,
          emptyCellPopupOpen: false,
          cell: action.cell,
          cellsRow: action.cellsRow,
        };
      } else {
        return {
          ...state,
          imagePopupOpen: true,
          emptyCellPopupOpen: false,
        };
      }
    }
    case SHOW_INFO_POPUP: {
      return {
        ...state,
        infoPopupOpen: true,
        cell: action.cell,
        cellsRow: action.cellsRow,
      };
    }
    case SHOW_FORMAT_CELLS_POPUP: {
      return {
        ...state,
        formatCellsPopupOpen: true,
      };
    }
    case CLOSE_POPUP: {
      return {
        ...state,
        raspberryPopupOpen: false,
        infoPopupOpen: false,
        actionPopupOpen: false,
        noTypePopupOpen: false,
        emptyCellPopupOpen: false,
        imagePopupOpen: false,
        formatCellsPopupOpen: false,
        addToUserPopupOpen: false,
        cell: undefined,
        cellsRow: undefined,
        cellFile: undefined,
        cellNameFile: undefined,
        cellImageFile: undefined,
      };
    }
    case GET_CELL_NAME_FILE_SUCCESS: {
      return {
        ...state,
        cellNameFile: action.file,
      };
    }
    case GET_CELL_IMAGE_FILE_SUCCESS: {
      return {
        ...state,
        cellImageFile: action.file,
      };
    }
    case GET_CELL_FILE_SUCCESS: {
      return {
        ...state,
        cellFile: action.file,
      };
    }
    default: {
      return state;
    }
  }
}
