import { makeStyles } from "@material-ui/core";
import PropTypes from "prop-types";
import {
  showEmptyCellPopup,
  showImagePopup,
} from "../../../state/actions/popups";
import { useDispatch } from "react-redux";
import { useEffect } from "react";
import { setBigImage } from "../../../state/actions/tables";

const useStyles = makeStyles({
  cell: {
    height: "50px",
    width: "100px",
    display: "table-cell",
    textAlign: "center",
    verticalAlign: "middle",
  },
});

const EmptyCell = ({ cell, cellsRow }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    if (cell && cell.bigImagePath !== undefined) {
      dispatch(setBigImage(cell));
    }
  }, [cell, dispatch]);

  const action = () => {
    if (cell.type === "empty") {
      dispatch(showEmptyCellPopup(cell, cellsRow));
    } else {
      dispatch(showImagePopup(cell, cellsRow));
    }
  };

  return (
    <div className={classes.cell} onClick={action}>
      {cell.type === "empty" ? "" : "image"}
    </div>
  );
};
EmptyCell.propTypes = {
  cell: PropTypes.object,
  cellsRow: PropTypes.object,
};
export default EmptyCell;
