import { Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { cancelIsSelecting } from "../../../state/actions/cellsRows";
import { showFormatCellsPopup } from "../../../state/actions/popups";

const useStyles = makeStyles({
  card: {
    marginLeft: "10px",
    background: "white",
    borderRadius: "10px",
    minHeight: "150px",
    flex: 1,
    padding: "20px",
  },
  cont: {
    display: "flex",
    flexDirection: "row",
  },
  text: {
    padding: "5px",
    borderRadius: "5px",
    border: "1px solid black",
    marginRight: "10px",
  },
  name: {
    margin: "0px",
    flex: 1,
  },
  description: {
    margin: "0px",
    flex: 1,
  },
  cellContainer: {
    display: "table",
  },
  deleteButton: {
    marginTop: "10px",
    background: "red",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
  formatButton: {
    marginRight: "20px",
    marginTop: "10px",
    marginBottom: "10px",
    background: "blue",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
  cancelButton: {
    marginTop: "10px",
    marginBottom: "10px",
    background: "red",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
});

const SelectedCellsFormat = () => {
  const classes = useStyles();
  const selectedCells = useSelector((store) => store.cellsRows.selectedCells);
  const letsUpdate = useSelector((store) => store.cellsRows.letsUpdate);
  const dispatch = useDispatch();

  const convertCellsName = (text) => {
    return text ? text.split("<br />").join(" ") : "";
  };

  const handleFormat = () => {
    dispatch(showFormatCellsPopup());
  };

  const handleCancel = () => {
    dispatch(cancelIsSelecting());
  };

  return (
    <div>
      <Button className={classes.formatButton} onClick={handleFormat}>
        Format Cells
      </Button>
      <Button className={classes.cancelButton} onClick={handleCancel}>
        CANCEL
      </Button>
      <h2>Select cells</h2>
      {letsUpdate && <div></div>}
      <div className={classes.cont}>
        {selectedCells &&
          selectedCells.map((cell, ix) => (
            <p key={ix} className={classes.text}>
              {cell.name === null ? "" : convertCellsName(cell.name)}
            </p>
          ))}
      </div>
      {!selectedCells || selectedCells.length === 0 ? (
        <p>No cells are selected</p>
      ) : (
        <span />
      )}
    </div>
  );
};
export default SelectedCellsFormat;
