import { makeStyles } from "@material-ui/core";
import { Button } from "@material-ui/core";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";

import { setSelectedTable } from "../../state/actions/tables";
import { cancelIsSelecting } from "../../state/actions/cellsRows";
import { cancelCustomCells } from "../../state/actions/popups";

const useStyles = makeStyles({
  buttonNormal: {
    color: "black",
    fontSize: "1em",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "black",
    borderWidth: "1px",
    margin: "10px 10px",
    whiteSpace: "wrap",
    borderStyle: "solid",
    padding: "5px",
    "&:hover": {
      color: "white",
      background: "black",
    },
  },
  buttonSelected: {
    color: "white",
    background: "black",
    fontSize: "1em",
    whiteSpace: "wrap",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "black",
    borderWidth: "1px",
    margin: "10px 10px",
    borderStyle: "solid",
    padding: "5px",
    "&:hover": {
      color: "white",
      background: "black",
      cursos: "auto",
    },
  },
});

const TablesListItem = ({ item }) => {
  const selectedTable = useSelector((store) => store.tables.selectedTable);
  const [isSelected, setIsSelected] = useState(false);
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    setIsSelected(selectedTable && selectedTable.id === item.id);
  }, [setIsSelected, selectedTable, item]);

  const selectTable = () => {
    dispatch(setSelectedTable(item));
    dispatch(cancelIsSelecting());
    dispatch(cancelCustomCells());
  };

  return (
    <Button
      onClick={isSelected ? null : selectTable}
      className={isSelected ? classes.buttonSelected : classes.buttonNormal}
    >
      {item.name}
    </Button>
  );
};
TablesListItem.propTypes = {
  item: PropTypes.object,
};
export default TablesListItem;
