import React from "react";
import { makeStyles, TextField, Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { FilePicker } from "react-file-picker";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { useEffect } from "react";

import { closePopup } from "../../state/actions/popups";
import {
  getCellFile,
  getCellNameFile,
  updateInfoCell,
  updateCell,
} from "../../state/actions/cell";
import { showSnackbar } from "../../state/actions/snackbar";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "95vh",
    marginTop: "calc(5vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  header: {
    margin: "10px 0px",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(5vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  button: {
    marginTop: "10px",
    marginBottom: "10px",
    border: "1px solid black",
  },
  submitButton: {
    marginTop: "10px",
    marginBottom: "10px",
    background: "green",
    color: "white",
    border: "1px solid black",
    "&:hover": {
      background: "black",
    },
  },
  quillContainer: {
    display: "flex",
    flexDirection: "row",
    marginTop: "10px",
  },
  editor: {
    marginLeft: "10px",
    flex: 1,
  },
  fileNameContainer: {
    marginTop: "10px",
    marginBottom: "20px",
  },
  removeFileButton: {
    marginLeft: "30px",
  },
  borderInputcontainer: {
    marginTop: "50px",
  },
  borderText: {
    marginBottom: "5px",
  },
  noTypeButton: {
    marginTop: "10px",
    background: "red",
    marginRight: "10px",
    marginBottom: "10px",
    color: "white",
    "&:hover": {
      background: "black",
    },
  },
});

const InfoCellPopup = () => {
  const [name, setName] = React.useState("");
  const [color, setColor] = React.useState("");
  const [typeForAction, setTypeForAction] = React.useState("");
  const [description, setDescription] = React.useState("");
  const [file, setFile] = React.useState(undefined);
  const [nameFile, setNameFile] = React.useState(undefined);
  const [top, setTop] = React.useState("");
  const [bottom, setBottom] = React.useState("");
  const [left, setLeft] = React.useState("");
  const [right, setRight] = React.useState("");
  const [baseTen, setBaseTen] = React.useState("");
  const [baseThree, setBaseThree] = React.useState("");
  const [baseHundred, setBaseHundred] = React.useState("");
  const [baseFour, setBaseFour] = React.useState("");
  const [databaseID, setDatabaseID] = React.useState("");
  const [fontSize, setFontSize] = React.useState("");

  const cell = useSelector((store) => store.popups.cell);
  const cellFile = useSelector((store) => store.popups.cellFile);
  const cellNameFile = useSelector((store) => store.popups.cellNameFile);
  const cellsRow = useSelector((store) => store.popups.cellsRow);

  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    if (cell) {
      setName(cell.name === undefined ? "" : cell.name);
      setColor(cell.color);
      setFontSize(cell.fontSize);
      setDescription(cell.description === undefined ? "" : cell.description);
      setTop(cell.topBorder);
      setBottom(cell.bottomBorder);
      setLeft(cell.leftBorder);
      setRight(cell.rightBorder);
      setBaseTen(cell.baseTen);
      setBaseFour(cell.baseFour);
      setBaseHundred(cell.baseHundred);
      setBaseThree(cell.baseThree);
      setDatabaseID(cell.databaseID);
      if (cell.nameFilePath !== undefined && cell.nameFilePath !== "") {
        dispatch(getCellNameFile(cell.nameFilePath));
      }
      if (cell.filePath !== undefined && cell.filePath !== "") {
        dispatch(getCellFile(cell.filePath));
      }
    }
  }, [cell, dispatch]);

  const handleSubmit = () => {
    dispatch(
      updateInfoCell(
        {
          ...cell,
          name: name,
          description: description === undefined ? "" : description,
          color,
          fontSize: fontSize === undefined ? null : fontSize,
          typeForAction: typeForAction === undefined ? "" : typeForAction,
          topBorder: top,
          bottomBorder: bottom,
          leftBorder: left,
          rightBorder: right,
          type: "info",
          baseTen: baseTen === undefined ? "" : fontSize,
          baseFour: baseFour === undefined ? "" : baseFour,
          baseHundred: baseHundred === undefined ? "" : baseHundred,
          baseThree: baseThree === undefined ? "" : baseThree,
          databaseID: databaseID === undefined ? "" : databaseID,
        },
        file,
        nameFile,
        cellsRow
      )
    );
    handleClose();
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  const handleNameChange = (value) => {
    setName(value);
  };

  const handleColorChange = (event) => {
    setColor(event.target.value);
  };

  const handleDescriptionChange = (value) => {
    setDescription(value);
  };

  const showError = (msg) => {
    dispatch(showSnackbar(msg));
  };

  const removeFile = () => {
    setFile(undefined);
  };

  const nameFileLoaded = (loadedFile) => {
    setNameFile(loadedFile);
  };

  const removeNameFile = () => {
    setNameFile(undefined);
  };

  const fileLoaded = (loadedFile) => {
    setFile(loadedFile);
  };

  const handleTypeForAction = (event) => {
    setTypeForAction(event.target.value);
  };

  const handleTopChange = (event) => {
    setTop(event.target.value);
  };

  const handleBottomChange = (event) => {
    setBottom(event.target.value);
  };

  const handleLeftChange = (event) => {
    setLeft(event.target.value);
  };

  const handleRightChange = (event) => {
    setRight(event.target.value);
  };

  const handleBaseTenChange = (event) => {
    setBaseTen(event.target.value);
  };

  const handleBaseFourChange = (event) => {
    setBaseFour(event.target.value);
  };

  const handleBaseThreeChange = (event) => {
    setBaseThree(event.target.value);
  };

  const handleBaseHundredChange = (event) => {
    setBaseHundred(event.target.value);
  };

  const handleDatabaseIDChange = (event) => {
    setDatabaseID(event.target.value);
  };

  const handleFontSize = (event) => {
    setFontSize(event.target.value);
  };

  const handleNoTypeSubmit = () => {
    dispatch(
      updateCell(
        {
          ...cell,
          name: "",
          description: "",
          color: "white",
          typeForAction: "",
          topBorder: 1,
          bottomBorder: 1,
          leftBorder: 1,
          rightBorder: 1,
          type: "notype",
          file: "",
          nameFile: "",
          fontSize: "",
          baseTen: "",
          baseHundred: "",
          baseThree: "",
          baseFour: "",
          databaseId: "",
        },
        cellsRow
      )
    );
    handleClose();
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Update Information</h2>
        <div className={classes.quillContainer}>
          <p className={classes.text}>Enter name:</p>
          <ReactQuill
            theme="snow"
            className={classes.editor}
            value={name}
            onChange={handleNameChange}
          />
        </div>
        <FilePicker
          extensions={["png", "jpg", "jpeg"]}
          onChange={nameFileLoaded}
          onError={showError}
        >
          <Button style={{ marginTop: "60px" }} className={classes.button}>
            Click to upload NAME image
          </Button>
        </FilePicker>
        {(nameFile || cellNameFile) && (
          <div className={classes.fileNameContainer}>
            Uploaded image:
            <strong>
              {nameFile === undefined ? cellNameFile.name : nameFile.name}
            </strong>
            <button
              className={classes.removeFileButton}
              onClick={removeNameFile}
            >
              Remove
            </button>
          </div>
        )}
        <h4>Hint: Standard mobile font is 12. 3 Rows can pass into cell.</h4>
        <TextField
          fullWidth
          label="Font size"
          name="font_size"
          variant="outlined"
          placeholder="Font size"
          value={fontSize}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleFontSize}
        />
        <h3>Format cell</h3>
        <TextField
          fullWidth
          label="Color"
          name="color"
          variant="outlined"
          placeholder="Cell color"
          value={color}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleColorChange}
        />
        <TextField
          fullWidth
          label="Type for action"
          name="type_for_action"
          variant="outlined"
          placeholder="Type for action"
          value={typeForAction}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleTypeForAction}
        />
        <FilePicker
          extensions={["pdf", "png", "jpg", "jpeg"]}
          onChange={fileLoaded}
          onError={showError}
        >
          <Button className={classes.button}>Click to upload file</Button>
        </FilePicker>
        {(file || cellFile) && (
          <div className={classes.fileNameContainer}>
            Uploaded file:{" "}
            <strong>{file === undefined ? cellFile.name : file.name}</strong>
            <button className={classes.removeFileButton} onClick={removeFile}>
              Remove
            </button>
          </div>
        )}
        <div className={classes.quillContainer}>
          <p className={classes.text}>Enter description:</p>
          <ReactQuill
            theme="snow"
            className={classes.editor}
            value={description}
            onChange={handleDescriptionChange}
          />
        </div>
        <div className={classes.borderInputcontainer}>
          <p className={classes.borderText}>Enter Borders Width In Numbers</p>
          <TextField
            name="top"
            variant="outlined"
            label="Top"
            placeholder="Top"
            value={top}
            style={{
              margin: "10px",
              maxWidth: "90px",
            }}
            onChange={handleTopChange}
          />
          <TextField
            label="Right"
            name="right"
            variant="outlined"
            placeholder="Right"
            value={right}
            style={{
              margin: "10px",
              maxWidth: "90px",
            }}
            onChange={handleRightChange}
          />
          <TextField
            label="Bottom"
            name="bottom"
            variant="outlined"
            placeholder="Bottom"
            value={bottom}
            style={{
              margin: "10px",
              maxWidth: "90px",
            }}
            onChange={handleBottomChange}
          />
          <TextField
            label="Left"
            name="left"
            variant="outlined"
            placeholder="Left"
            value={left}
            style={{
              margin: "10px",
              maxWidth: "90px",
            }}
            onChange={handleLeftChange}
          />
        </div>
        <h2>Bluetooth</h2>
        <TextField
          fullWidth
          label="Base 10"
          name="base10"
          variant="outlined"
          placeholder="Base 10"
          value={baseTen}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleBaseTenChange}
        />
        <TextField
          fullWidth
          label="Base 44"
          name="base44"
          variant="outlined"
          placeholder="Base 44"
          value={baseFour}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleBaseFourChange}
        />
        <TextField
          fullWidth
          label="Base 100"
          name="base100"
          variant="outlined"
          placeholder="Base 100"
          value={baseHundred}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleBaseHundredChange}
        />
        <TextField
          fullWidth
          label="Base 336"
          name="base336"
          variant="outlined"
          placeholder="Base 336"
          value={baseThree}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleBaseThreeChange}
        />
        <TextField
          fullWidth
          label="Database ID"
          name="databaseID"
          variant="outlined"
          placeholder="Database ID"
          value={databaseID}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleDatabaseIDChange}
        />
        <div>
          {cell.type !== "notype" && (
            <Button
              className={classes.noTypeButton}
              onClick={handleNoTypeSubmit}
            >
              MAKE NO TYPE
            </Button>
          )}
          <Button className={classes.submitButton} onClick={handleSubmit}>
            Submit
          </Button>
        </div>
      </div>
    </div>
  );
};
export default InfoCellPopup;
