import {
  GET_CELLS_ROWS,
  GET_CELLS_ROWS_SUCCESS,
  GET_CELLS_ROWS_FAILED,
  ADD_SELECTING_CELL,
  BEGIN_FORMAT_SELECTING,
  STOP_FORMAT_SELECTING,
  UPDATE_CELLS_ROWS,
} from "../actionTypes/cellsRows";
import { db } from "../../firebase";

export function getCellsRows(tableId) {
  return async (dispatch) => {
    dispatch({ type: GET_CELLS_ROWS });

    try {
      const ref = db.collection("tables").doc(tableId).collection("cellsRows");
      ref.get().then((snapshot) => {
        const data = [];
        snapshot.forEach((cellsRow) => {
          data.push({ ...cellsRow.data(), id: cellsRow.id });
        });
        dispatch({ type: GET_CELLS_ROWS_SUCCESS, data: data });
      });
    } catch (e) {
      const error = e && e.error ? e.error : "System error";
      dispatch({
        type: GET_CELLS_ROWS_FAILED,
        error,
      });
    }
  };
}

export function submitSelectingCells(cellInfo, cells, cellsRows) {
  return async (dispatch) => {
    if (cellInfo.isBoxBorder) {
      boxUpdate(dispatch, cellInfo, cells, cellsRows);
    } else {
      simpleUpdate(dispatch, cellInfo, cells, cellsRows);
    }
  };
}

const boxUpdate = (dispatch, cellInfo, cells, cellsRows) => {
  cells.sort((a, b) => (a.rowIndex > b.rowIndex ? 1 : -1));
  const smallestRow = cells[0].rowIndex;
  const bigestRow =
    cells.length > 1 ? cells[cells.length - 1].rowIndex : smallestRow;
  cells.sort((a, b) => (a.columnIndex > b.columnIndex ? 1 : -1));
  const smallestColumn = cells[0].columnIndex;
  const bigestColumn =
    cells.length > 1 ? cells[cells.length - 1].columnIndex : smallestColumn;

  const rowsToUpdate = [...cellsRows];
  const cellsToUpdate = [];
  const rowsForRequest = [];
  cells.forEach((cell) => {
    const newCell = {
      ...cell,
      color: cellInfo.color !== "" ? cellInfo.color : cell.color,
      bottomBorder:
        cell.rowIndex === bigestRow ? cellInfo.width : cell.bottomBorder,
      topBorder:
        cell.rowIndex === smallestRow ? cellInfo.width : cell.topBorder,
      leftBorder:
        cell.columnIndex === smallestColumn ? cellInfo.width : cell.leftBorder,
      rightBorder:
        cell.columnIndex === bigestColumn ? cellInfo.width : cell.rightBorder,
    };
    cellsToUpdate.push(newCell);
  });
  cellsToUpdate.forEach((cell, index) => {
    rowsToUpdate[cell.rowIndex] = getFormattedRow(
      cell,
      cells[index],
      rowsToUpdate[cell.rowIndex]
    );
    if (!rowsForRequest.includes(cell.rowIndex)) {
      rowsForRequest.push(cell.rowIndex);
    }
  });
  rowsToUpdate.forEach((row) => {
    if (rowsForRequest.includes(row.rowIndex)) {
      sendUpdateRowRequest(row);
    }
  });
  dispatch({ type: UPDATE_CELLS_ROWS, cellsRows: rowsToUpdate });
};

const simpleUpdate = (dispatch, cellInfo, cells, cellsRows) => {
  const rowsToUpdate = [...cellsRows];
  const cellsToUpdate = [];
  const rowsForRequest = [];
  cells.forEach((cell) => {
    const newCell = {
      ...cell,
      color: cellInfo.color !== "" ? cellInfo.color : cell.color,
      bottomBorder:
        cellInfo.bottom !== "" ? cellInfo.bottom : cell.bottomBorder,
      topBorder: cellInfo.top !== "" ? cellInfo.top : cell.topBorder,
      leftBorder: cellInfo.left !== "" ? cellInfo.left : cell.leftBorder,
      rightBorder: cellInfo.right !== "" ? cellInfo.right : cell.rightBorder,
    };
    cellsToUpdate.push(newCell);
  });
  cellsToUpdate.forEach((cell, index) => {
    rowsToUpdate[cell.rowIndex] = getFormattedRow(
      cell,
      cells[index],
      rowsToUpdate[cell.rowIndex]
    );
    if (!rowsForRequest.includes(cell.rowIndex)) {
      rowsForRequest.push(cell.rowIndex);
    }
  });
  rowsToUpdate.forEach((row) => {
    if (rowsForRequest.includes(row.rowIndex)) {
      sendUpdateRowRequest(row);
    }
  });
  dispatch({ type: UPDATE_CELLS_ROWS, cellsRows: rowsToUpdate });
};

const sendUpdateRowRequest = (row) => {
  db.collection("tables")
    .doc(row.tableId)
    .collection("cellsRows")
    .doc(row.id)
    .update({ cells: row.cells });
};

const getFormattedRow = (cell, mirrorCell, row) => {
  const cellIndex = row.cells.indexOf(mirrorCell);
  row.cells[cellIndex] = cell;
  return row;
};

export function addSelectingCell(cell) {
  return async (dispatch) => {
    dispatch({ type: ADD_SELECTING_CELL, cell });
  };
}

export function setIsSelecting() {
  return async (dispatch) => {
    dispatch({ type: BEGIN_FORMAT_SELECTING });
  };
}

export function cancelIsSelecting() {
  return async (dispatch) => {
    dispatch({ type: STOP_FORMAT_SELECTING });
  };
}
