import {
  GET_TABLES,
  GET_TABLES_SUCCESS,
  GET_TABLES_FAILED,
  ADD_TABLE,
  SET_SELECTED_TABLE,
  DELETE_TABLE,
  DELETE_TABLE_SUCCESS,
  DELETE_TABLE_FAILED,
  SET_BIG_IMAGE,
  SET_TABLE_JUST_CREATED_FALSE,
  REMOVE_BIG_IMAGE,
} from "../actionTypes/tables";

const initialState = {
  tables: [],
  selectedTable: undefined,
  tableJustCreated: false,
  bigImage: undefined,
  bigImageCell: undefined,
  error: undefined,
};

export default function tablesReducer(state = initialState, action = {}) {
  switch (action.type) {
    case GET_TABLES: {
      return {
        ...state,
        error: undefined,
      };
    }
    case REMOVE_BIG_IMAGE: {
      return {
        ...state,
        bigImage: undefined,
        bigImageCell: undefined,
        error: undefined,
      };
    }
    case SET_TABLE_JUST_CREATED_FALSE: {
      return {
        ...state,
        tableJustCreated: false,
      };
    }
    case GET_TABLES_SUCCESS: {
      return { ...state, tables: action.tables };
    }
    case GET_TABLES_FAILED: {
      return {
        ...state,
        error: true,
      };
    }
    case ADD_TABLE: {
      const newTables = state.tables;
      newTables.push(action.table);
      return {
        ...state,
        tableJustCreated: true,
        selectedTable: action.table,
        tables: newTables,
      };
    }
    case SET_BIG_IMAGE: {
      return {
        ...state,
        bigImage: action.image,
        bigImageCell: action.cell,
      };
    }
    case SET_SELECTED_TABLE: {
      return {
        ...state,
        selectedTable: action.table,
        bigImage: undefined,
        bigImageCell: undefined,
      };
    }
    case DELETE_TABLE: {
      return {
        ...state,
        error: undefined,
      };
    }
    case DELETE_TABLE_SUCCESS: {
      const newTables = state.tables;
      state.tables.forEach((table) => {
        if (table.id === action.id) {
          newTables.remove(table);
        }
      });
      return {
        ...state,
        selectedTable: undefined,
        tables: newTables,
      };
    }
    case DELETE_TABLE_FAILED: {
      return {
        ...state,
        error: true,
      };
    }
    default: {
      return state;
    }
  }
}
