import { useSelector } from "react-redux";

const BigImage = () => {
  const image = useSelector((store) => store.tables.bigImage);
  const cell = useSelector((store) => store.tables.bigImageCell);

  return image !== undefined ? (
    <img
      style={{
        position: "absolute",
        top: `${
          cell.rowIndex * 50 +
          140 +
          64 +
          56 +
          parseInt(cell.bigImageTopMargin) +
          25 -
          cell.bigImageHeight / 2
        }px`,
        left: `${
          cell.columnIndex * 100 +
          180 +
          parseInt(cell.bigImageLeftMargin) +
          50 -
          cell.bigImageWidth / 2
        }px`,
      }}
      src={image}
      height={cell.bigImageHeight}
      width={cell.bigImageWidth}
      alt="big"
    />
  ) : (
    <div></div>
  );
};
export default BigImage;
