import {
  SIGN_IN,
  SIGN_IN_SUCCESS,
  SIGN_IN_FAILED,
  GET_USERS,
  GET_USERS_SUCCESS,
  GET_USERS_FAILED,
  ADD_TABLE_TO_USER,
  ADD_TABLE_TO_USER_SUCCESS,
  ADD_TABLE_TO_USER_FAILED,
} from "../actionTypes/user";

const initialState = {
  user: undefined,
  users: [],
  error: false,
};

export default function userReducer(state = initialState, action = {}) {
  switch (action.type) {
    case ADD_TABLE_TO_USER_FAILED:
    case ADD_TABLE_TO_USER_SUCCESS:
    case ADD_TABLE_TO_USER: {
      return { ...state };
    }
    case SIGN_IN: {
      return { ...state, error: false };
    }
    case SIGN_IN_SUCCESS: {
      return { ...state, user: action.user };
    }
    case SIGN_IN_FAILED: {
      return {
        ...state,
        error: true,
      };
    }
    case GET_USERS_SUCCESS: {
      return { ...state, users: action.data };
    }
    default: {
      return state;
    }
  }
}
