import React from "react";
import {
  Checkbox,
  FormControlLabel,
  makeStyles,
  TextField,
  Button,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";

import { closePopup } from "../../state/actions/popups";
import { submitSelectingCells } from "../../state/actions/cellsRows";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "95vh",
    marginTop: "calc(5vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  header: {
    margin: "10px 0px",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(5vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  borderInputcontainer: {
    marginTop: "10px",
  },
  borderText: {
    marginBottom: "5px",
  },
  submitButton: {
    marginTop: "10px",
    marginBottom: "10px",
    background: "green",
    color: "white",
    border: "1px solid black",
    "&:hover": {
      background: "black",
    },
  },
});

const FormatCellsPopup = () => {
  const [color, setColor] = React.useState("");
  const [width, setWidth] = React.useState("");
  const [top, setTop] = React.useState("");
  const [bottom, setBottom] = React.useState("");
  const [left, setLeft] = React.useState("");
  const [right, setRight] = React.useState("");
  const [isBoxBorder, setIsBoxBorder] = React.useState(false);
  const selectedCells = useSelector((store) => store.cellsRows.selectedCells);
  const cellsRows = useSelector((store) => store.cellsRows.cellsRows);

  const classes = useStyles();
  const dispatch = useDispatch();

  const handleSubmit = () => {
    dispatch(
      submitSelectingCells(
        { color, top, bottom, left, right, isBoxBorder, width },
        selectedCells,
        cellsRows
      )
    );
    handleClose();
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  const handleColorChange = (event) => {
    setColor(event.target.value);
  };

  const handleWidthChange = (event) => {
    setWidth(event.target.value);
  };

  const handleTopChange = (event) => {
    setTop(event.target.value);
  };

  const handleBottomChange = (event) => {
    setBottom(event.target.value);
  };

  const handleLeftChange = (event) => {
    setLeft(event.target.value);
  };

  const handleRightChange = (event) => {
    setRight(event.target.value);
  };

  const handleBoxBorder = () => {
    setIsBoxBorder(!isBoxBorder);
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Format Cells</h2>
        <TextField
          fullWidth
          label="Color"
          name="color"
          variant="outlined"
          placeholder="Cell color"
          value={color}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleColorChange}
        />
        <div>
          <FormControlLabel
            value={isBoxBorder}
            control={<Checkbox color="default" onClick={handleBoxBorder} />}
            label="Is box border?"
            checked={isBoxBorder}
            labelPlacement="start"
          />
        </div>
        {!isBoxBorder && (
          <div className={classes.borderInputcontainer}>
            <p className={classes.borderText}>Enter Borders Width In Numbers</p>
            <TextField
              name="top"
              variant="outlined"
              label="Top"
              placeholder="Top"
              value={top}
              style={{
                margin: "10px",
                maxWidth: "90px",
              }}
              onChange={handleTopChange}
            />
            <TextField
              label="Right"
              name="right"
              variant="outlined"
              placeholder="Right"
              value={right}
              style={{
                margin: "10px",
                maxWidth: "90px",
              }}
              onChange={handleRightChange}
            />
            <TextField
              label="Bottom"
              name="bottom"
              variant="outlined"
              placeholder="Bottom"
              value={bottom}
              style={{
                margin: "10px",
                maxWidth: "90px",
              }}
              onChange={handleBottomChange}
            />
            <TextField
              label="Left"
              name="left"
              variant="outlined"
              placeholder="Left"
              value={left}
              style={{
                margin: "10px",
                maxWidth: "90px",
              }}
              onChange={handleLeftChange}
            />
          </div>
        )}
        {isBoxBorder && (
          <div>
            <TextField
              label="Border width"
              name="width"
              variant="outlined"
              placeholder="Border width"
              value={width}
              style={{
                margin: "10px",
              }}
              onChange={handleWidthChange}
            />
          </div>
        )}
        <Button className={classes.submitButton} onClick={handleSubmit}>
          Submit
        </Button>
      </div>
    </div>
  );
};
export default FormatCellsPopup;
