import { makeStyles } from "@material-ui/core";
import { Card, Button } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import { useSelector, useDispatch } from "react-redux";
import PropTypes from "prop-types";
import TablesListItem from "./TablesListItem";
import { useEffect, useState } from "react";
import { getTables } from "../../state/actions/tables";
import { getUsers } from "../../state/actions/user";
import { showRaspPopup } from "../../state/actions/popups";

const useStyles = makeStyles({
  card: {
    display: "inline-flex",
    flexDirection: "column",
    background: "white",
    borderRadius: "10px",
    minHeight: "150px",
    maxWidth: "150px",
    flex: 1,
  },
  raspberrybutton: {
    height: "35px",
    color: "white",
    background: "black",
    fontSize: "1.2em",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "#FFFFFF",
    borderWidth: "1px",
    margin: "0px auto 5px auto",
    borderStyle: "solid",
    "&:hover": {
      color: "black",
      background: "white",
      borderColor: "black",
    },
  },
  iconmenubutton: {
    height: "35px",
    color: "black",
    fontSize: "2em",
    textTransform: "none",
    borderRadius: "5px",
    borderColor: "#FFFFFF",
    borderWidth: "1px",
    margin: "0px auto 5px auto",
    borderStyle: "solid",
    "&:hover": {
      color: "white",
      background: "black",
    },
  },
  text: {
    textAlign: "center",
    margin: "5px",
  },
});

const TablesList = ({ showTableNamePopup }) => {
  const [didGetUsers, setDidGetUsers] = useState(false);
  const classes = useStyles();
  const tables = useSelector((store) => store.tables.tables);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getTables());
    if (!didGetUsers) {
      dispatch(getUsers());
      setDidGetUsers(true);
    }
  }, [dispatch, didGetUsers]);

  const toggleRaspPopup = () => {
    dispatch(showRaspPopup());
  };

  return (
    <Card className={classes.card}>
      <Button className={classes.raspberrybutton} onClick={toggleRaspPopup}>
        RASPBERRY
      </Button>
      <h3 className={classes.text}>Tables</h3>
      <Button className={classes.iconmenubutton} onClick={showTableNamePopup}>
        <AddIcon />
      </Button>
      {tables.map((item) => (
        <TablesListItem item={item} key={tables.indexOf(item)} />
      ))}
    </Card>
  );
};
TablesList.propTypes = {
  showTableNamePopup: PropTypes.func,
};
export default TablesList;
