import { Button } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import { removeBigImage } from "../../../state/actions/tables";

const RemoveBigImage = () => {
  const image = useSelector((store) => store.tables.bigImage);
  const dispatch = useDispatch();

  const removeBigImageFromTable = () => {
    dispatch(removeBigImage());
  };

  return image !== undefined ? (
    <Button
      style={{
        color: "white",
        background: "red",
        marginBottom: "10px",
        marginTop: "10px",
      }}
      onClick={removeBigImageFromTable}
    >
      TEMPORARILY REMOVE BIG IMAGE ( UNTIL REFRESH )
    </Button>
  ) : (
    <div></div>
  );
};
export default RemoveBigImage;
