import React from "react";
import {
  Checkbox,
  FormControlLabel,
  makeStyles,
  TextField,
} from "@material-ui/core";
import { Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { useEffect } from "react";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";

import { beginCustomSelect, closePopup } from "../../state/actions/popups";
import { updateCell } from "../../state/actions/cell";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
    "&:hover": {
      background: "black",
    },
  },
  formControl: {
    display: "flex",
    marginBottom: "10px",
    minWidth: 200,
  },
  header: {
    margin: "10px 0px",
  },
  noTypeButton: {
    marginTop: "10px",
    background: "red",
    marginRight: "10px",
    color: "white",
    "&:hover": {
      background: "black",
    },
  },
  quillContainer: {
    display: "flex",
    flexDirection: "row",
    marginTop: "10px",
  },
  editor: {
    marginLeft: "10px",
    flex: 1,
  },
});

const ActionCellPopup = () => {
  const [sumAll, setSumAll] = React.useState(false);
  const [name, setName] = React.useState("");
  const [color, setColor] = React.useState("");
  const [rows, setRows] = React.useState("");
  const [actionType, setActionType] = React.useState("");
  const [typeActionType, setTypeActionType] = React.useState("");
  const [typeActionTypeName, setTypeActionTypeName] = React.useState("");
  const [fontSize, setFontSize] = React.useState("");

  const cell = useSelector((store) => store.popups.cell);
  const cellsRow = useSelector((store) => store.popups.cellsRow);

  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    if (cell.sumAll) {
      setSumAll(cell.sumAll);
    } else {
      setName(cell.name === undefined ? "" : cell.name);
      setFontSize(cell.fontSize);
      setActionType(cell.actionType);
      setTypeActionType(cell.typeActionType);
      setTypeActionTypeName(cell.typeActionTypeName);
      setColor(cell.color);
      setRows(cell.rows);
    }
  }, [cell]);

  const actionTypes = ["selectrows", "selecttype", "custom"];
  const typeActionTypes = ["selecttypeall", "selecttyperows"];
  const actionTypesNames = ["Select Rows", "Select Type", "Custom"];
  const typeActionTypesNames = ["Select All Cells of Type", "Select Type Rows"];

  const handleNameChange = (value) => {
    setName(value);
  };

  const handleTypeNameChange = (event) => {
    setTypeActionTypeName(event.target.value);
  };

  const handleColorChange = (event) => {
    setColor(event.target.value);
  };

  const handleSubmit = () => {
    if (actionType === "custom") {
      dispatch(
        beginCustomSelect({
          cell: {
            ...cell,
            name: name === undefined ? "" : name.split("\n").join("<br />"),
            color: color === undefined ? "" : color,
            actionType: actionType === undefined ? "" : actionType,
            typeActionType: typeActionType === undefined ? "" : typeActionType,
            typeActionTypeName:
              typeActionTypeName === undefined ? "" : typeActionTypeName,
            fontSize: fontSize === undefined ? "" : fontSize,
            rows: rows === undefined ? "" : rows,
            sumAll,
            type: "action",
          },
        })
      );
    } else {
      dispatch(
        updateCell(
          {
            ...cell,
            name: name === undefined ? "" : name.split("\n").join("<br />"),
            color: color === undefined ? "" : color,
            actionType: actionType === undefined ? "" : actionType,
            typeActionType: typeActionType === undefined ? "" : typeActionType,
            typeActionTypeName:
              typeActionTypeName === undefined ? "" : typeActionTypeName,
            fontSize: fontSize === undefined ? "" : fontSize,
            rows: rows === undefined ? "" : rows,
            sumAll,
            type: "action",
          },
          cellsRow
        )
      );
      handleClose();
    }
  };

  const handleNoTypeSubmit = () => {
    dispatch(
      updateCell(
        {
          ...cell,
          name: "",
          color: "white",
          actionType: "",
          typeActionType: "",
          typeActionTypeName: "",
          fontSize: "",
          rows: "",
          sumAll: false,
          type: "notype",
        },
        cellsRow
      )
    );
    handleClose();
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  const handleRowsChange = (event) => {
    setRows(event.target.value);
  };

  const handleActionTypeChange = (event) => {
    setActionType(event.target.value);
  };

  const handleTypeActionTypeChange = (event) => {
    setTypeActionType(event.target.value);
  };

  const handleSumAll = () => {
    setSumAll(!sumAll);
  };

  const handleFontSize = (event) => {
    setFontSize(event.target.value);
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Update Action</h2>
        <div>
          <FormControlLabel
            value={sumAll}
            control={<Checkbox color="default" onClick={handleSumAll} />}
            label="Sum all?"
            checked={sumAll}
            labelPlacement="start"
          />
        </div>
        {!sumAll && (
          <div>
            <div className={classes.quillContainer}>
              <p className={classes.text}>Enter name:</p>
              <ReactQuill
                theme="snow"
                className={classes.editor}
                value={name}
                onChange={handleNameChange}
              />
            </div>
            <h4 style={{ marginTop: "60px" }}>
              Hint: Standard mobile font is 12. 3 Rows can pass into cell.
            </h4>
            <TextField
              fullWidth
              label="Font size"
              name="font_size"
              variant="outlined"
              placeholder="Font size"
              value={fontSize}
              style={{ marginBottom: "10px", marginTop: "10px" }}
              onChange={handleFontSize}
            />
            <h3>Format cell</h3>
            <TextField
              fullWidth
              label="Color"
              name="color"
              variant="outlined"
              placeholder="Cell color"
              value={color}
              style={{ marginBottom: "10px", marginTop: "10px" }}
              onChange={handleColorChange}
            />
            <FormControl className={classes.formControl}>
              <InputLabel id="label">Action Type</InputLabel>
              <Select
                id="select"
                value={actionType || ""}
                onChange={handleActionTypeChange}
              >
                {actionTypes.map((item, ix) => (
                  <MenuItem key={item} value={item}>
                    {actionTypesNames[ix]}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            {actionType === actionTypes[1] && (
              <FormControl className={classes.formControl}>
                <InputLabel id="label">Action Type</InputLabel>
                <Select
                  id="select"
                  value={typeActionType || ""}
                  onChange={handleTypeActionTypeChange}
                >
                  {typeActionTypes.map((item, ix) => (
                    <MenuItem key={item} value={item}>
                      {typeActionTypesNames[ix]}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            )}
            {actionType === actionTypes[1] && (
              <TextField
                fullWidth
                label="Type name"
                name="Type name"
                variant="outlined"
                placeholder="Enter type name"
                value={typeActionTypeName || ""}
                style={{ marginBottom: "10px", marginTop: "10px" }}
                onChange={handleTypeNameChange}
              />
            )}
            {(actionType === actionTypes[0] ||
              (actionType === actionTypes[1] &&
                typeActionType === typeActionTypes[1])) && (
              <TextField
                fullWidth
                label="Rows"
                name="rows"
                variant="outlined"
                placeholder="Enter row indexes separated with Whitespaces ( Begining with 0 )"
                value={rows || ""}
                style={{ marginBottom: "10px", marginTop: "10px" }}
                onChange={handleRowsChange}
              />
            )}
          </div>
        )}
        {cell.type !== "notype" && (
          <Button className={classes.noTypeButton} onClick={handleNoTypeSubmit}>
            MAKE NO TYPE
          </Button>
        )}
        <Button className={classes.button} onClick={handleSubmit}>
          Submit
        </Button>
      </div>
    </div>
  );
};

export default ActionCellPopup;
