import {
  SIGN_IN,
  SIGN_IN_SUCCESS,
  SIGN_IN_FAILED,
  GET_USERS,
  GET_USERS_SUCCESS,
  GET_USERS_FAILED,
  ADD_TABLE_TO_USER,
  ADD_TABLE_TO_USER_SUCCESS,
  ADD_TABLE_TO_USER_FAILED,
  ADD_RASP_TO_USER,
  ADD_RASP_TO_USER_SUCCESS,
  ADD_RASP_TO_USER_FAILED,
} from "../actionTypes/user";
import { auth, db } from "../../firebase";

export function signIn(user) {
  return async (dispatch) => {
    dispatch({ type: SIGN_IN });
    try {
      auth
        .signInWithEmailAndPassword(user.email, user.password)
        .then((value) => {
          db.collection("users")
            .doc(value.user.uid)
            .get()
            .then((snapshot) => {
              const dbUser = snapshot.data();
              if (dbUser.role === "admin") {
                dispatch({ type: SIGN_IN_SUCCESS, user: dbUser });
              } else {
                dispatch({ type: SIGN_IN_FAILED });
              }
            });
        });
    } catch (e) {
      dispatch({ type: SIGN_IN_FAILED });
    }
  };
}

export function getUsers() {
  return async (dispatch) => {
    dispatch({ type: GET_USERS });
    try {
      db.collection("users")
        .get()
        .then((snapshot) => {
          const data = [];
          snapshot.forEach((snap) => {
            data.push({ ...snap.data(), id: snap.id });
          });
          dispatch({ type: GET_USERS_SUCCESS, data });
        });
    } catch (e) {
      dispatch({ type: GET_USERS_FAILED });
    }
  };
}

export function addTableToUser(user, table) {
  return async (dispatch) => {
    dispatch({ type: ADD_TABLE_TO_USER });
    try {
      const ref = db.collection("users").doc(user.id).collection("tables");
      ref.get().then((snapshots) => {
        ref.add({ tableId: table.id, tableOrder: snapshots.size });
        dispatch({ type: ADD_TABLE_TO_USER_SUCCESS });
      });
    } catch (e) {
      dispatch({ type: ADD_TABLE_TO_USER_FAILED });
    }
  };
}

export function addRaspberryToUser(user, rasp) {
  return async (dispatch) => {
    const url =
      "https://fm.ki.lt/api/v1/dataexchange/mappings?api_token=6c3ec4bbb801163e98fec95acf555193fea410ea21270ab5fd3c58f8c264fba1d1df794fb2ee97a24ab5d7de424e3010";
    console.log(user);
    console.log(rasp);
    dispatch({ type: ADD_RASP_TO_USER });
    try {
      const data = {
        userId: user.id,
        raspberryId: rasp,
      };
      const req = new Request(url, {
        method: "POST",
        body: JSON.stringify(data),
        headers: {
          "Content-Type": "application/json",
        },
      });
      const response = await fetch(req);
      if (response.ok) {
        dispatch({ type: ADD_RASP_TO_USER_SUCCESS });
      } else {
        dispatch({ type: ADD_RASP_TO_USER_FAILED });
      }
    } catch (e) {
      dispatch({ type: ADD_RASP_TO_USER_FAILED });
    }
  };
}
