import { Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import React from "react";
import {
  makeStyles,
  FormControl,
  Select,
  InputLabel,
  MenuItem,
} from "@material-ui/core";

import { closePopup } from "../../state/actions/popups";
import { addTableToUser } from "../../state/actions/user";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  makeEmptyButton: {
    border: "1px solid black",
    color: "white",
    background: "blue",
    marginTop: "10px",
    marginRight: "20px",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
  },
  formControl: {
    display: "flex",
    marginBottom: "10px",
    minWidth: 200,
  },
  header: {
    margin: "10px 0px",
  },
});

const AddToUserPopup = () => {
  const [selectedUser, setSelectedUser] = React.useState(undefined);
  const users = useSelector((store) => store.user.users);
  const selectedTable = useSelector((store) => store.tables.selectedTable);

  const classes = useStyles();
  const dispatch = useDispatch();

  const handleSubmit = () => {
    dispatch(addTableToUser(selectedUser, selectedTable));
    handleClose();
  };

  const handleChange = (event) => {
    setSelectedUser(event.target.value);
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Select user</h2>
        <div>
          <FormControl className={classes.formControl}>
            <InputLabel id="label">User</InputLabel>
            <Select
              id="demo-simple-select-helper"
              value={selectedUser || ""}
              label="User"
              onChange={handleChange}
            >
              {users.map((user, ix) => (
                <MenuItem key={ix} value={user}>
                  <em>{user.email}</em>
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </div>
        <Button className={classes.button} onClick={handleSubmit}>
          Submit
        </Button>
      </div>
    </div>
  );
};
export default AddToUserPopup;
