import React from "react";
import { makeStyles, TextField } from "@material-ui/core";
import { Button } from "@material-ui/core";
import { useDispatch } from "react-redux";
import ReactQuill from "react-quill";

import { addTable } from "../../state/actions/tables";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  quillContainer: {
    display: "flex",
    flexDirection: "row",
    marginTop: "10px",
  },
  editor: {
    marginLeft: "10px",
    flex: 1,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  button: {
    marginTop: "60px",
    border: "1px solid black",
    background: "green",
    color: "white",
    "&:hover": {
      background: "black",
    },
  },
});

const TableInfoPopup = (props) => {
  const [name, setName] = React.useState("");
  const [description, setDescription] = React.useState("");
  const classes = useStyles();
  const dispatch = useDispatch();

  const handleSubmit = () => {
    dispatch(addTable(name, description));
    props.handleClose();
  };

  const handleNameChange = (event) => {
    setName(event.target.value);
  };

  const handleDescriptionChange = (value) => {
    setDescription(value);
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={props.handleClose}>
          x
        </span>
        <h2>Create table</h2>
        <TextField
          required
          fullWidth
          label="Name"
          name="name"
          variant="outlined"
          placeholder="Name"
          value={name}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleNameChange}
        />
        <div className={classes.quillContainer}>
          <p className={classes.text}>Enter description:</p>
          <ReactQuill
            theme="snow"
            className={classes.editor}
            value={description}
            onChange={handleDescriptionChange}
          />
        </div>
        <Button className={classes.button} onClick={handleSubmit}>
          Submit
        </Button>
      </div>
    </div>
  );
};

export default TableInfoPopup;
