import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import {
  showActionPopup,
  showInfoPopup,
  showNoTypePopup,
  cellToUpdateCustomCellSelected,
} from "../../../state/actions/popups";
import { addSelectingCell } from "../../../state/actions/cellsRows";

const useStyles = makeStyles({
  cell: {
    background: (props) => props.color,
    borderLeftWidth: (props) => props.left + "px",
    borderRightWidth: (props) => props.right + "px",
    borderTopWidth: (props) => props.top + "px",
    borderBottomWidth: (props) => props.bottom + "px",
    borderTopColor: (props) => (props.isCustomCell ? "#2BFF00" : "black"),
    borderRightColor: (props) => (props.isCustomCell ? "#2BFF00" : "black"),
    borderLeftColor: (props) => (props.isCustomCell ? "#2BFF00" : "black"),
    borderBottomColor: (props) => (props.isCustomCell ? "#2BFF00" : "black"),
    height: "50px",
    width: "100px",
    borderLeftStyle: "solid",
    borderRightStyle: "solid",
    borderTopStyle: "solid",
    borderBottomStyle: "solid",
    alignContent: "center",
    textAlign: "center",
    justifyContent: "center",
    display: "table-cell",
    verticalAlign: "middle",
  },
  text: {
    fontSize: "10px",
    margin: "0px",
    lineHeight: "1.1",
    fontWeight: "bold",
  },
});

const Cell = ({ cell, cellsRow, isCustomCell }) => {
  const classes = useStyles({
    left: cell.leftBorder,
    right: cell.rightBorder,
    top: cell.topBorder,
    bottom: cell.bottomBorder,
    color: cell.color,
    isCustomCell,
  });
  const cellToUpdate = useSelector((store) => store.popups.cellToUpdate);
  const isSelecting = useSelector((store) => store.cellsRows.isSelecting);

  const dispatch = useDispatch();

  const action = () => {
    if (isSelecting) {
      dispatch(addSelectingCell(cell));
    } else if (cellToUpdate !== undefined) {
      if (
        !(
          cellToUpdate.cell.columnIndex === cell.columnIndex &&
          cellToUpdate.cell.rowIndex === cell.rowIndex
        ) &&
        cell.type === "info"
      ) {
        dispatch(cellToUpdateCustomCellSelected(cell));
      }
    } else if (cell.type === "notype") {
      dispatch(showNoTypePopup(cell, cellsRow));
    } else if (cell.type === "info") {
      dispatch(showInfoPopup(cell, cellsRow));
    } else if (cell.type === "action") {
      dispatch(showActionPopup(cell, cellsRow));
    }
  };

  return (
    <div className={classes.cell} onClick={action}>
      {cell.sumAll && (
        <img
          style={{
            width: "25px",
            height: "25px",
            marginLeft: "auto",
            marginRight: "auto",
          }}
          src={"/dao.png"}
          alt="all"
        />
      )}
      {cell.name && !cell.sumAll && (
        <div
          dangerouslySetInnerHTML={{
            __html: cell.name
              .replaceAll("<p>", "<span>")
              .replaceAll("</p>", "</span><br/>"),
          }}
        ></div>
      )}
    </div>
  );
};
Cell.propTypes = {
  cell: PropTypes.object,
  cellsRow: PropTypes.object,
  isCustomCell: PropTypes.bool,
};
export default Cell;
