import { db, storage } from "../../firebase";
import {
  UPDATE_CELL,
  UPDATE_CELL_SUCCESS,
  UPDATE_CELL_FAILED,
  GET_CELL_FILE,
  GET_CELL_FILE_SUCCESS,
  GET_CELL_FILE_FAILED,
  GET_CELL_NAME_FILE,
  GET_CELL_NAME_FILE_SUCCESS,
  GET_CELL_NAME_FILE_FAILED,
  GET_CELL_IMAGE_FILE,
  GET_CELL_IMAGE_FILE_SUCCESS,
  GET_CELL_IMAGE_FILE_FAILED,
} from "../actionTypes/cell";
import { CLOSE_POPUP, CUSTOM_CELLS_SUBMITED } from "../actionTypes/popups";

export function updateCell(cell, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: UPDATE_CELL });
    try {
      const ref = db
        .collection("tables")
        .doc(cell.tableId)
        .collection("cellsRows")
        .doc(cell.cellsRowId);
      const newRow = [];
      cellsRow.cells.forEach((element) => {
        if (element.columnIndex !== cell.columnIndex) {
          newRow.push(element);
        }
      });
      newRow.push(cell);
      ref.update({
        cells: newRow,
      });
      dispatch({
        type: UPDATE_CELL_SUCCESS,
        cell,
      });
      dispatch({ type: CUSTOM_CELLS_SUBMITED });
    } catch (e) {
      const error = e && e.error ? e.error : "System error";
      dispatch({
        type: UPDATE_CELL_FAILED,
        error,
      });
    }
    dispatch({ type: CLOSE_POPUP });
  };
}

export function updateImageCell(cell, file, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: UPDATE_CELL });
    try {
      let bigImagePath =
        "/" +
        cell.tableId +
        "/" +
        cell.cellsRowId +
        "/" +
        cell.columnIndex +
        "/" +
        file.name;
      await storage.ref(bigImagePath).put(file);
      const ref = db
        .collection("tables")
        .doc(cell.tableId)
        .collection("cellsRows")
        .doc(cell.cellsRowId);
      const newCell = { ...cell, bigImagePath };
      const newRow = [];
      cellsRow.cells.forEach((element) => {
        if (element.columnIndex !== newCell.columnIndex) {
          newRow.push(element);
        }
      });
      newRow.push(newCell);
      ref.update({
        cells: newRow,
      });
      dispatch({
        type: UPDATE_CELL_SUCCESS,
        cell: newCell,
      });
    } catch (e) {
      dispatch({
        type: UPDATE_CELL_FAILED,
        e,
      });
    }
    dispatch({ type: CLOSE_POPUP });
  };
}

export function getCellImageFile(bigImagePath) {
  return async (dispatch) => {
    dispatch({ type: GET_CELL_IMAGE_FILE });
    try {
      storage
        .ref(bigImagePath)
        .getMetadata()
        .then((file) => {
          dispatch({ type: GET_CELL_IMAGE_FILE_SUCCESS, file });
        });
    } catch (e) {
      const error = e && e.error ? e.error : "System error";
      dispatch({
        type: GET_CELL_IMAGE_FILE_FAILED,
        error,
      });
    }
  };
}

