export function updateInfoCell(cell, file, nameFile, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: UPDATE_CELL });
    try {
      let filePath = "";
      let nameFilePath = "";
      if (file !== undefined) {
        filePath =
          "/" +
          cell.tableId +
          "/" +
          cell.cellsRowId +
          "/" +
          cell.columnIndex +
          "/" +
          file.name;
        await storage.ref(filePath).put(file);
      } else if (cell.filePath && cell.filePath !== "") {
        filePath = cell.filePath;
      }
      if (nameFile !== undefined) {
        nameFilePath =
          "/" +
          cell.tableId +
          "/" +
          cell.cellsRowId +
          "/" +
          cell.columnIndex +
          "/" +
          nameFile.name;
        await storage.ref(nameFilePath).put(nameFile);
      } else if (cell.nameFilePath && cell.nameFilePath !== "") {
        nameFilePath = cell.nameFilePath;
      }
      const ref = db
        .collection("tables")
        .doc(cell.tableId)
        .collection("cellsRows")
        .doc(cell.cellsRowId);
      const newCell = { ...cell, filePath, nameFilePath };
      const newRow = [];
      cellsRow.cells.forEach((element) => {
        if (element.columnIndex !== newCell.columnIndex) {
          newRow.push(element);
        }
      });
      newRow.push(newCell);
      console.log(newCell);
      ref.update({
        cells: newRow,
      });
      dispatch({
        type: UPDATE_CELL_SUCCESS,
        cell: newCell,
      });
    } catch (e) {
      const error = e && e.error ? e.error : "System error";
      console.log(error);
      console.log(e);
      dispatch({
        type: UPDATE_CELL_FAILED,
        error,
      });
    }
    dispatch({ type: CLOSE_POPUP });
  };
}
