import { Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import React from "react";
import { Checkbox, FormControlLabel, makeStyles } from "@material-ui/core";

import { closePopup, submitNoTypePopup } from "../../state/actions/popups";
import { updateCell } from "../../state/actions/cell";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  makeEmptyButton: {
    border: "1px solid black",
    color: "white",
    background: "blue",
    marginTop: "10px",
    marginRight: "20px",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
  },
  formControl: {
    display: "flex",
    marginBottom: "10px",
    minWidth: 200,
  },
  header: {
    margin: "10px 0px",
  },
});

const NoTypeCellPopup = () => {
  const [isAction, setIsAction] = React.useState(false);
  const cell = useSelector((store) => store.popups.cell);
  const cellsRow = useSelector((store) => store.popups.cellsRow);

  const classes = useStyles();
  const dispatch = useDispatch();

  const handleIsAction = () => {
    setIsAction(!isAction);
  };

  const handleSubmit = () => {
    dispatch(submitNoTypePopup(isAction));
  };

  const handleMakeEmptySubmit = () => {
    dispatch(updateCell({ ...cell, type: "empty" }, cellsRow));
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <h2 className={classes.header}>Select cell type</h2>
        <div>
          <FormControlLabel
            value={isAction}
            control={<Checkbox color="default" onClick={handleIsAction} />}
            label="Is it an action cell?"
            checked={isAction}
            labelPlacement="start"
          />
        </div>
        <Button
          className={classes.makeEmptyButton}
          onClick={handleMakeEmptySubmit}
        >
          Make Empty
        </Button>
        <Button className={classes.button} onClick={handleSubmit}>
          Submit Type Cell
        </Button>
      </div>
    </div>
  );
};
export default NoTypeCellPopup;
