import {
  GET_CELLS_ROWS,
  GET_CELLS_ROWS_SUCCESS,
  GET_CELLS_ROWS_FAILED,
  CLEAR_CELLS_ROWS,
  ADD_SELECTING_CELL,
  BEGIN_FORMAT_SELECTING,
  STOP_FORMAT_SELECTING,
  UPDATE_CELLS_ROWS,
} from "../actionTypes/cellsRows";
import { UPDATE_CELL_SUCCESS } from "../actionTypes/cell";
const initialState = {
  cellsRows: [],
  selectedCells: undefined,
  error: undefined,
  letsUpdate: false,
  isSelecting: false,
};

export default function cellsRowsReducer(state = initialState, action = {}) {
  switch (action.type) {
    case UPDATE_CELL_SUCCESS: {
      let rowIndex;
      let cellIndex;
      state.cellsRows.forEach((cellsRow) => {
        cellsRow.cells.forEach((cell) => {
          if (
            cell.columnIndex === action.cell.columnIndex &&
            cell.rowIndex === action.cell.rowIndex
          ) {
            rowIndex = state.cellsRows.indexOf(cellsRow);
            cellIndex = cellsRow.cells.indexOf(cell);
          }
        });
      });
      state.cellsRows[rowIndex].cells[cellIndex] = action.cell;
      return { ...state, error: undefined };
    }
    case BEGIN_FORMAT_SELECTING: {
      return {
        ...state,
        isSelecting: true,
        selectedCells: [],
      };
    }
    case STOP_FORMAT_SELECTING: {
      return {
        ...state,
        isSelecting: false,
        selectedCells: undefined,
      };
    }
    case ADD_SELECTING_CELL: {
      var cells = state.selectedCells;
      if (cells.includes(action.cell)) {
        cells.splice(cells.indexOf(action.cell), 1);
      } else {
        cells.push(action.cell);
      }
      return {
        ...state,
        selectedCells: cells,
        letsUpdate: !state.letsUpdate,
      };
    }
    case GET_CELLS_ROWS: {
      return {
        ...state,
        error: undefined,
      };
    }
    case GET_CELLS_ROWS_SUCCESS: {
      action.data.sort((a, b) => (a.rowIndex > b.rowIndex ? 1 : -1));
      return { ...state, cellsRows: action.data };
    }
    case UPDATE_CELLS_ROWS: {
      action.cellsRows.sort((a, b) => (a.rowIndex > b.rowIndex ? 1 : -1));
      return {
        ...state,
        cellsRows: action.cellsRows,
        selectedCells: undefined,
        isSelecting: false,
      };
    }
    case GET_CELLS_ROWS_FAILED: {
      return {
        ...state,
        error: true,
      };
    }
    case CLEAR_CELLS_ROWS: {
      return {
        ...state,
        cellsRows: [],
      };
    }
    default: {
      return state;
    }
  }
}
