import React from "react";
import { Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import { cancelCustomCells } from "../../../state/actions/popups";
import { updateCell } from "../../../state/actions/cell";

const useStyles = makeStyles({
  container: {
    marginBottom: "10px",
    marginTop: "10px",
    background: "white",
    padding: "20px",
  },
  cont: {
    display: "flex",
    flexDirection: "row",
  },
  text: {
    padding: "5px",
    borderRadius: "5px",
    border: "1px solid black",
    marginRight: "10px",
  },
  submitButton: {
    marginTop: "10px",
    background: "blue",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
  cancelButton: {
    marginTop: "10px",
    marginRight: "20px",
    background: "red",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
});

const SelectedCellsInfo = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const customCells = useSelector(
    (store) => store.popups.cellToUpdateCustomCells
  );
  const cell = useSelector((store) => store.popups.cellToUpdate);
  const letsUpdate = useSelector((store) => store.popups.letsUpdate);
  const cellsRow = useSelector((store) => store.popups.cellsRow);

  const submitCustomCellsF = () => {
    dispatch(
      updateCell(
        {
          ...cell.cell,
          customCells: convertCustomCells(customCells),
        },
        cellsRow
      )
    );
  };

  const convertCustomCells = (cells) => {
    const data = [];
    cells.forEach((element) => {
      data.push({
        columnIndex: element.columnIndex,
        rowIndex: element.rowIndex,
      });
    });
    return data;
  };

  const cancelCustomCellsF = () => {
    dispatch(cancelCustomCells());
  };

  const convertCustomCellsName = (text) => {
    return text.split("<br />").join(" ");
  };

  return customCells !== undefined ? (
    <div className={classes.container}>
      {letsUpdate && <div></div>}
      <h2>Select cells</h2>
      <div className={classes.cont}>
        {customCells.map((cell, ix) => (
          <p key={ix} className={classes.text}>
            {cell.name === null ? "" : convertCustomCellsName(cell.name)}
          </p>
        ))}
        {customCells.length === 0 ? (
          <p className={classes.text}>No cells are selected</p>
        ) : (
          <p />
        )}
      </div>
      <Button className={classes.cancelButton} onClick={cancelCustomCellsF}>
        Cancel
      </Button>
      <Button className={classes.submitButton} onClick={submitCustomCellsF}>
        Submit custom cells
      </Button>
    </div>
  ) : (
    <div></div>
  );
};
export default SelectedCellsInfo;
