import { makeStyles, TextField, Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { FilePicker } from "react-file-picker";
import React from "react";
import { useEffect } from "react";

import { closePopup } from "../../state/actions/popups";
import { updateImageCell, getCellImageFile } from "../../state/actions/cell";
import { showSnackbar } from "../../state/actions/snackbar";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  fileNameContainer: {
    marginTop: "10px",
    marginBottom: "20px",
  },
  removeFileButton: {
    marginLeft: "30px",
  },
  fileButton: {
    marginTop: "10px",
    marginBottom: "10px",
    border: "1px solid black",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
    "&:hover": {
      background: "black",
    },
  },
});

const ImageCellPopup = () => {
  const [width, setWidth] = React.useState("");
  const [height, setHeight] = React.useState("");
  const [file, setFile] = React.useState(undefined);
  const [leftMargin, setLeftMargin] = React.useState("");
  const [topMargin, setTopMargin] = React.useState("");

  const cell = useSelector((store) => store.popups.cell);
  const cellFile = useSelector((store) => store.popups.cellImageFile);
  const cellsRow = useSelector((store) => store.popups.cellsRow);

  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    if (cell) {
      setHeight(cell.bigImageHeight === undefined ? "" : cell.bigImageHeight);
      setWidth(cell.bigImageWidth === undefined ? "" : cell.bigImageWidth);
      setLeftMargin(
        cell.bigImageLeftMargin === undefined ? "" : cell.bigImageLeftMargin
      );
      setTopMargin(
        cell.bigImageTopMargin === undefined ? "" : cell.bigImageTopMargin
      );
      if (cell && cell.bigImagePath !== undefined && cell.bigImagePath !== "") {
        dispatch(getCellImageFile(cell.bigImagePath));
      }
    }
  }, [cell, dispatch]);

  const handleWidthChange = (event) => {
    setWidth(event.target.value);
  };

  const handleHeightChange = (event) => {
    setHeight(event.target.value);
  };

  const handleTopMarginChange = (event) => {
    setTopMargin(event.target.value);
  };

  const handleLeftMarginChange = (event) => {
    setLeftMargin(event.target.value);
  };

  const fileLoaded = (loadedFile) => {
    setFile(loadedFile);
  };

  const removeFile = () => {
    setFile(undefined);
  };

  const handleSubmit = () => {
    dispatch(
      updateImageCell(
        {
          ...cell,
          type: "image",
          bigImageWidth: width,
          bigImageHeight: height,
          bigImageTopMargin: topMargin === "" ? 0 : topMargin,
          bigImageLeftMargin: leftMargin === "" ? 0 : leftMargin,
        },
        file,
        cellsRow
      )
    );
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  const showError = (msg) => {
    dispatch(showSnackbar(msg));
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <FilePicker
          extensions={["png", "jpg", "jpeg"]}
          onChange={fileLoaded}
          onError={showError}
        >
          <Button className={classes.fileButton}>Click to upload image</Button>
        </FilePicker>
        {(file || cellFile) && (
          <div className={classes.fileNameContainer}>
            Uploaded image:{" "}
            <strong>{file === undefined ? cellFile.name : file.name}</strong>
            <button className={classes.removeFileButton} onClick={removeFile}>
              Remove
            </button>
          </div>
        )}
        <h3>Single cell width = 100 and height = 50</h3>
        <TextField
          fullWidth
          label="Width"
          name="width"
          variant="outlined"
          placeholder="Enter width"
          value={width}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleWidthChange}
        />
        <TextField
          fullWidth
          label="Height"
          name="height"
          variant="outlined"
          placeholder="Enter height"
          value={height}
          style={{ marginBottom: "10px", marginTop: "10px" }}
          onChange={handleHeightChange}
        />
        <div style={{ marginBottom: "10px", marginTop: "10px" }}>
          <TextField
            label="Top margin"
            name="topMargin"
            variant="outlined"
            placeholder="Top margin"
            value={topMargin}
            style={{
              marginRight: "10px",
              maxWidth: "140px",
            }}
            onChange={handleTopMarginChange}
          />
          <TextField
            label="Left margin"
            name="leftMargin"
            variant="outlined"
            placeholder="Left margin"
            value={leftMargin}
            style={{
              maxWidth: "140px",
            }}
            onChange={handleLeftMarginChange}
          />
        </div>
        <Button className={classes.button} onClick={handleSubmit}>
          SUBMIT
        </Button>
      </div>
    </div>
  );
};
export default ImageCellPopup;
