export function getEmptyTableCells(tableId) {
  const data = [];
  data.push(getFirstEmptyRow(tableId));
  for (let i = 1; i < 28; i++) {
    data.push(getEmptyRow(i, tableId));
  }
  return data;
}
function getFirstEmptyRow(tableId) {
  return {
    tableId,
    rowIndex: 0,
    cells: [
      getNoTypeCell(0, 0),
      getNoTypeCell(0, 1),
      getNoTypeCell(0, 2),
      getNoTypeCell(0, 3),
      getEmptyCell(4),
      getEmptyCell(5),
      getEmptyCell(6),
      getNoTypeCell(0, 7),
      getNoTypeCell(0, 8),
      getNoTypeCell(0, 9),
      getNoTypeCell(0, 10),
    ],
  };
}
function getEmptyRow(rowIndex, tableId) {
  return {
    tableId,
    rowIndex,
    cells: [
      getNoTypeCell(rowIndex, 0),
      getNoTypeCell(rowIndex, 1),
      getNoTypeCell(rowIndex, 2),
      getNoTypeCell(rowIndex, 3),
      getNoTypeCell(rowIndex, 4),
      getNoTypeCell(rowIndex, 5),
      getNoTypeCell(rowIndex, 6),
      getNoTypeCell(rowIndex, 7),
      getNoTypeCell(rowIndex, 8),
      getNoTypeCell(rowIndex, 9),
      getNoTypeCell(rowIndex, 10),
    ],
  };
}
function getNoTypeCell(rowIndex, columnIndex) {
  return {
    type: "notype",
    rowIndex,
    columnIndex,
    color: "white",
    topBorder: "1",
    leftBorder: "1",
    rightBorder: "1",
    bottomBorder: "1",
  };
}
function getEmptyCell(columnIndex) {
  return {
    type: "empty",
    rowIndex: 0,
    columnIndex,
    color: "white",
    topBorder: "1",
    leftBorder: "1",
    rightBorder: "1",
    bottomBorder: "1",
  };
}
