import { Button } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import { closePopup, showImagePopup } from "../../state/actions/popups";
import { updateCell } from "../../state/actions/cell";

const useStyles = makeStyles({
  popupbox: {
    position: "absolute",
    background: "#00000050",
    width: "100%",
    height: "100vh",
    top: 0,
    left: 0,
  },
  box: {
    position: "relative",
    width: "70%",
    margin: "0 auto",
    height: "auto",
    maxHeight: "70vh",
    marginTop: "calc(15vh - 20px)",
    background: "white",
    borderRadius: "4px",
    padding: "20px",
    border: "1px solid black",
    overflow: "auto",
  },
  closeicon: {
    content: "x",
    cursor: "pointer",
    position: "fixed",
    right: "calc(15% - 12.5px)",
    top: "calc(15vh - 32.5px)",
    background: "white",
    width: "25px",
    height: "25px",
    borderRadius: "50%",
    lineHeight: "20px",
    textAlign: "center",
    border: "1px solid black",
    fontSize: "20px",
    fontWeight: "bold",
  },
  button: {
    border: "1px solid black",
    background: "green",
    color: "white",
    marginTop: "10px",
    "&:hover": {
      background: "black",
    },
  },
  notypeButton: {
    border: "1px solid black",
    background: "blue",
    marginRight: "20px",
    color: "white",
    marginTop: "10px",
    "&:hover": {
      background: "black",
    },
  },
});

const EmptyCellPopup = () => {
  const cell = useSelector((store) => store.popups.cell);
  const cellsRow = useSelector((store) => store.popups.cellsRow);

  const classes = useStyles();
  const dispatch = useDispatch();

  const handleSubmit = () => {
    dispatch(updateCell({ ...cell, type: "notype" }, cellsRow));
  };

  const handleImageSubmit = () => {
    dispatch(showImagePopup());
  };

  const handleClose = () => {
    dispatch(closePopup());
  };

  return (
    <div className={classes.popupbox}>
      <div className={classes.box}>
        <span className={classes.closeicon} onClick={handleClose}>
          x
        </span>
        <Button className={classes.notypeButton} onClick={handleSubmit}>
          MAKE CELL NOTYPE
        </Button>
        <Button className={classes.button} onClick={handleImageSubmit}>
          ADD IMAGE
        </Button>
      </div>
    </div>
  );
};
export default EmptyCellPopup;
