import React from "react";
import { useSelector } from "react-redux";
import { Container } from "@material-ui/core";
import { useEffect, useState } from "react";
import { disableBodyScroll, enableBodyScroll } from "body-scroll-lock";
import { useHistory } from "react-router-dom";

import TablesList from "../tables_list/TablesList";
import Table from "../table/Table";
import TableInfoPopup from "../popup/TableInfoPopup";
import ActionCellPopup from "../popup/ActionCellPopup";
import NoTypeCellPopup from "../popup/NoTypeCellPopup";
import InfoCellPopup from "../popup/InfoCellPopup";
import EmptyCellPopup from "../popup/EmptyCellPopup";
import ImageCellPopup from "../popup/ImageCellPopup";
import FormatCellsPopup from "../popup/FormatCellsPopup";
import AddToUserPopup from "../popup/AddToUserPopup";
import MapRaspberryPopup from "../popup/MapRaspberryPopup";

const Home = () => {
  const [isNamePopupOpen, setIsNamePopupOpen] = useState(false);
  const selectedTable = useSelector((store) => store.tables.selectedTable);
  const isActionCellPopupOpen = useSelector(
    (store) => store.popups.actionPopupOpen
  );
  const isEmptyCellPopupOpen = useSelector(
    (store) => store.popups.emptyCellPopupOpen
  );
  const isInfoCellPopupOpen = useSelector(
    (store) => store.popups.infoPopupOpen
  );
  const noTypeCellPopupOpen = useSelector(
    (store) => store.popups.noTypePopupOpen
  );
  const imageCellPopupOpen = useSelector(
    (store) => store.popups.imagePopupOpen
  );
  const formatCellsPopupOpen = useSelector(
    (store) => store.popups.formatCellsPopupOpen
  );
  const addToUserPopupOpen = useSelector(
    (store) => store.popups.addToUserPopupOpen
  );
  const raspberryPopupOpen = useSelector(
    (store) => store.popups.raspberryPopupOpen
  );

  const user = useSelector((store) => store.user.user);

  const history = useHistory();

  const popupOpened = () => {
    window.scrollTo(0, 0);
    disableBodyScroll(document);
  };

  isNamePopupOpen ||
  isActionCellPopupOpen ||
  isInfoCellPopupOpen ||
  imageCellPopupOpen ||
  isEmptyCellPopupOpen ||
  noTypeCellPopupOpen ||
  formatCellsPopupOpen ||
  addToUserPopupOpen ||
  raspberryPopupOpen
    ? popupOpened()
    : enableBodyScroll(document);

  useEffect(() => {
    if (user === undefined || user.role !== "admin") {
      history.push("/");
    }
    window.scrollTo(0, 0);
  }, [user, history]);

  const toggleNamePopup = () => {
    setIsNamePopupOpen(!isNamePopupOpen);
  };

  return (
    <Container
      style={{
        padding: "10px",
        width: "100%",
        display: "inline-flex",
        flexDirection: "row",
      }}
    >
      <TablesList showTableNamePopup={toggleNamePopup} />
      {selectedTable === undefined ? (
        <h2 style={{ flex: 1, textAlign: "center" }}>Please select a table</h2>
      ) : (
        <Table table={selectedTable} />
      )}
      {isNamePopupOpen && <TableInfoPopup handleClose={toggleNamePopup} />}
      {isEmptyCellPopupOpen && <EmptyCellPopup />}
      {isActionCellPopupOpen && <ActionCellPopup />}
      {isInfoCellPopupOpen && <InfoCellPopup />}
      {noTypeCellPopupOpen && <NoTypeCellPopup />}
      {imageCellPopupOpen && <ImageCellPopup />}
      {formatCellsPopupOpen && <FormatCellsPopup />}
      {addToUserPopupOpen && <AddToUserPopup />}
      {raspberryPopupOpen && <MapRaspberryPopup />}
    </Container>
  );
};
export default Home;
