import { Button, Card, CircularProgress } from "@material-ui/core";
import { makeStyles } from "@material-ui/core";
import PropTypes from "prop-types";
import { useSelector, useDispatch } from "react-redux";
import { getCellsRows, setIsSelecting } from "../../state/actions/cellsRows";
import { useEffect, useState, useCallback } from "react";
import CellsRow from "./CellsRow";
import {
  deleteTable,
  setTableJustCreatedFalse,
} from "../../state/actions/tables";
import SelectedCellsInfo from "./utils/SelectedCellsInfo";
import BigImage from "./utils/BigImage";
import RemoveBigImage from "./utils/RemoveBigImage";
import SelectedCellsFormat from "./utils/SelectedCellsFormat";
import { showAddToUserPopup } from "../../state/actions/popups";

const useStyles = makeStyles({
  card: {
    marginLeft: "10px",
    background: "white",
    borderRadius: "10px",
    minHeight: "150px",
    flex: 1,
    padding: "20px",
  },
  name: {
    margin: "0px",
    flex: 1,
  },
  description: {
    height: "20px",
    margin: "0px",
    flex: 1,
  },
  cellContainer: {
    display: "table",
  },
  deleteButton: {
    marginTop: "10px",
    background: "red",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
  addToUserButton: {
    marginRight: "10px",
    marginTop: "10px",
    background: "blue",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
  formatButton: {
    marginRight: "10px",
    marginTop: "10px",
    marginBottom: "10px",
    background: "blue",
    color: "white",
    padding: "10px 20px",
    "&:hover": {
      background: "black",
    },
  },
});

const Table = ({ table }) => {
  const classes = useStyles();
  const [loading, setLoading] = useState(false);
  const cellsRows = useSelector((store) => store.cellsRows.cellsRows);
  const tableJustCreated = useSelector(
    (store) => store.tables.tableJustCreated
  );
  const isSelecting = useSelector((store) => store.cellsRows.isSelecting);

  const dispatch = useDispatch();

  const timeoutFinished = useCallback(() => {
    dispatch(getCellsRows(table.id));
    dispatch(setTableJustCreatedFalse());
  }, [dispatch, table]);

  useEffect(() => {
    if (!tableJustCreated) {
      if (!loading) {
        dispatch(getCellsRows(table.id));
      }
      setLoading(false);
    } else {
      setLoading(true);
      setTimeout(timeoutFinished, 3000);
    }
  }, [dispatch, table, tableJustCreated, loading, timeoutFinished]);

  const handleDelete = () => {
    dispatch(deleteTable(table.id, cellsRows));
  };

  const handleAddToUser = () => {
    dispatch(showAddToUserPopup());
  };

  const handleSelect = () => {
    dispatch(setIsSelecting());
  };

  return (
    <Card className={classes.card}>
      <h2 className={classes.name}>{table.name}</h2>
      <div
        dangerouslySetInnerHTML={{
          __html: table.description
            .replaceAll("<p", "<span")
            .replaceAll("</p>", "</span><br/>"),
        }}
      ></div>
      <RemoveBigImage />
      <SelectedCellsInfo />
      {loading && <CircularProgress />}
      {!loading && (
        <div>
          {!isSelecting && (
            <Button className={classes.formatButton} onClick={handleSelect}>
              Select Cells
            </Button>
          )}
          {isSelecting && <SelectedCellsFormat />}
          <div className={classes.cellContainer}>
            <BigImage />
            {cellsRows.map((cellsRow) => (
              <CellsRow cellsRow={cellsRow} key={cellsRows.indexOf(cellsRow)} />
            ))}
          </div>
          <div>
            <Button
              className={classes.addToUserButton}
              onClick={handleAddToUser}
            >
              ADD TABLE TO USER
            </Button>
            <Button className={classes.deleteButton} onClick={handleDelete}>
              Delete
            </Button>
          </div>
        </div>
      )}
    </Card>
  );
};
Table.propTypes = {
  table: PropTypes.object,
};
export default Table;
