import {
  GET_TABLES,
  GET_TABLES_SUCCESS,
  GET_TABLES_FAILED,
  DELETE_TABLE,
  DELETE_TABLE_SUCCESS,
  DELETE_TABLE_FAILED,
  ADD_TABLE,
  SET_SELECTED_TABLE,
  SET_BIG_IMAGE,
  SET_TABLE_JUST_CREATED_FALSE,
  REMOVE_BIG_IMAGE,
} from "../actionTypes/tables";
import { db, storage } from "../../firebase";
import { getEmptyTableCells } from "../../common/CellFunctions";

export function removeBigImage() {
  return async (dispatch) => {
    dispatch({ type: REMOVE_BIG_IMAGE });
  };
}

export function getTables() {
  return async (dispatch) => {
    dispatch({ type: GET_TABLES });

    try {
      const ref = db.collection("tables");
      await ref.get().then((snapshot) => {
        const data = [];
        snapshot.forEach((table) => {
          data.push({ ...table.data(), id: table.id });
        });
        dispatch({ type: GET_TABLES_SUCCESS, tables: data });
      });
    } catch (e) {
      const error = e && e.error ? e.error : "System error";
      dispatch({
        type: GET_TABLES_FAILED,
        error,
      });
    }
  };
}

export function setTableJustCreatedFalse() {
  return async (dispatch) => {
    dispatch({
      type: SET_TABLE_JUST_CREATED_FALSE,
    });
  };
}

export function addTable(name, description) {
  return async (dispatch) => {
    const ref = db.collection("tables");
    await ref.add({ name, description }).then((table) => {
      const cellsRows = getEmptyTableCells(table.id);
      cellsRows.forEach((cellsRow) => {
        ref
          .doc(table.id)
          .collection("cellsRows")
          .add({
            tableId: cellsRow.tableId,
            rowIndex: cellsRow.rowIndex,
          })
          .then((cellsRowValue) => {
            const newCells = [];
            cellsRow.cells.forEach((cell) =>
              newCells.push({
                ...cell,
                tableId: table.id,
                cellsRowId: cellsRowValue.id,
              })
            );
            ref
              .doc(table.id)
              .collection("cellsRows")
              .doc(cellsRowValue.id)
              .update({ cells: newCells });
          });
      });

      dispatch({
        type: ADD_TABLE,
        table: { id: table.id, name, description },
      });
    });
  };
}

export function deleteTable(tableId, cellsRows) {
  return async (dispatch) => {
    dispatch({ type: DELETE_TABLE });
    try {
      var ref = db.collection("tables").doc(tableId);
      cellsRows.forEach((row) => {
        ref.collection("cellsRows").doc(row.id).delete();
      });
      ref.delete();
      dispatch({ type: DELETE_TABLE_SUCCESS, tableId });
    } catch (e) {
      const error = e && e.error ? e.error : "System error";
      dispatch({
        type: DELETE_TABLE_FAILED,
        error,
      });
    }
  };
}

export function setBigImage(cell) {
  return async (dispatch) => {
    storage
      .ref(cell.bigImagePath)
      .getDownloadURL()
      .then((image) => {
        dispatch({ type: SET_BIG_IMAGE, image, cell });
      })
      .catch(() => {
        console.log("error");
      });
  };
}

export function setSelectedTable(table) {
  return async (dispatch) => {
    dispatch({ type: SET_SELECTED_TABLE, table });
  };
}
