import {
  SHOW_RASP_POPUP,
  SHOW_ACTION_POPUP,
  CLOSE_POPUP,
  SHOW_INFO_POPUP,
  SHOW_NO_TYPE_POPUP,
  SHOW_IMAGE_CELL_POPUP,
  SUBMIT_NO_TYPE_POPUP,
  BEGIN_CUSTOM_SELECT,
  CELL_TO_UPDATE_CUSTOM_CELL_SELECTED,
  CANCEL_CUSTOM_CELLS,
  SHOW_EMPTY_CELL_POPUP,
  SHOW_FORMAT_CELLS_POPUP,
  SHOW_ADD_TO_USER_POPUP,
} from "../actionTypes/popups";

export function beginCustomSelect(cell) {
  return async (dispatch) => {
    dispatch({ type: BEGIN_CUSTOM_SELECT, cell });
  };
}

export function cancelCustomCells() {
  return async (dispatch) => {
    dispatch({ type: CANCEL_CUSTOM_CELLS });
  };
}

export function cellToUpdateCustomCellSelected(cell) {
  return async (dispatch) => {
    dispatch({ type: CELL_TO_UPDATE_CUSTOM_CELL_SELECTED, cell });
  };
}

export function showFormatCellsPopup() {
  return async (dispatch) => {
    dispatch({ type: SHOW_FORMAT_CELLS_POPUP });
  };
}

export function showImagePopup(cell, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: SHOW_IMAGE_CELL_POPUP, cell, cellsRow });
  };
}

export function showActionPopup(cell, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: SHOW_ACTION_POPUP, cell, cellsRow });
  };
}

export function showEmptyCellPopup(cell, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: SHOW_EMPTY_CELL_POPUP, cell, cellsRow });
  };
}

export function submitNoTypePopup(isAction) {
  return async (dispatch) => {
    dispatch({ type: SUBMIT_NO_TYPE_POPUP, isAction });
  };
}

export function showNoTypePopup(cell, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: SHOW_NO_TYPE_POPUP, cell, cellsRow });
  };
}

export function showInfoPopup(cell, cellsRow) {
  return async (dispatch) => {
    dispatch({ type: SHOW_INFO_POPUP, cell, cellsRow });
  };
}

export function showAddToUserPopup() {
  return async (dispatch) => {
    dispatch({ type: SHOW_ADD_TO_USER_POPUP });
  };
}

export function showRaspPopup() {
  return async (dispatch) => {
    dispatch({ type: SHOW_RASP_POPUP });
  };
}

export function closePopup() {
  return async (dispatch) => {
    dispatch({ type: CLOSE_POPUP });
  };
}
